/**
 * „É≠„Éº„ÉÄ„Éº
 * @param {string} target - „É≠„Éº„ÉÄ„Éº„ÇíÊåøÂÖ•„Åô„Çã„Çø„Éº„Ç≤„ÉÉ„ÉàÔºàÊåáÂÆö„Åå„Å™„Åë„Çå„Å∞bodyÔºâ
 * @param {string} msg - „É≠„Éº„ÉÄ„Éº„ÅÆ„É°„ÉÉ„Çª„Éº„Ç∏Ôºà„Ç≠„É£„Éó„Ç∑„Éß„É≥Ôºâ default: 'Loading...'
 * @param {string} size - „É≠„Éº„ÉÄ„Éº„ÅÆ„Çµ„Ç§„Ç∫Ôºà„Çπ„Çø„Ç§„É´„Ç∑„Éº„Éà„ÇíÂèÇÁÖßÔºâ default: 'md'
 * @param {string} style - „É≠„Éº„ÉÄ„Éº„ÅÆ„Çπ„Çø„Ç§„É´Ôºà„Çπ„Çø„Ç§„É´„Ç∑„Éº„Éà„ÇíÂèÇÁÖßÔºâ default: '--bg-black'
 */
class RfLoader {
  #settings = {
    target: document.body,
    msg: 'Loading...',
    size: 'md',
    style: '--bg-black',
    loaderClass: 'c-loader',
    loader: null,
  }
  constructor(target, msg, size, style) {
    if (target) this.#settings.target = target
    if (msg) this.#settings.msg = msg
    if (size) this.#settings.size = size
    if (style) this.#settings.style = style
    console.log(this.#settings)
    this.#init()
  }

  #insertLoader(loader) {
    this.#settings.target.insertAdjacentHTML('beforeend', loader)
    this.#settings.loader = this.#settings.target.querySelector('.' + this.#settings.loaderClass) //„É≠„Éº„ÉÄ„ÉºË¶ÅÁ¥†„Çí‰øùÁÆ°
    console.log(this.#settings.loader)
  }

  removeLoader() {
    // console.log('„É≠„Éº„ÉÄ„Éº„ÇíÂâäÈô§')
    this.#settings.loader.remove()
  }

  #makeLoader() {
    let size = ''
    if (this.#settings.size !== 'md') size = `--${this.#settings.size}`
    const loader = `<div class="${this.#settings.loaderClass} ${this.#settings.style} ${size}"></div>`
    this.#insertLoader(loader)
  }

  #init() {
    this.#makeLoader()
  }
}

export default RfLoader